.error_text { height: 565px; width: 100%; background-color: #0B63AC; padding: 20rem; color: white; }

.error_text p { font-size: 28px; font-weight: 100; margin-bottom: -0.8rem; }

.error_text h1 { font-weight: 800; text-transform: uppercase; font-size: 50px; }

