.sys_msg { position: fixed; top: 8rem; right: 9rem; z-index: 9999999; }

.sys_msg_text { display: flex; -webkit-box-align: center; -ms-flex-align: center; align-items: center; background: #143B64; opacity: .9; padding: 1.6rem 2rem; }

