/* Tooltip container */
.tooltip { position: relative; display: inline-block; cursor: pointer; border-bottom: unset !important; }

/* Tooltip text */
.tooltip .tooltiptext { visibility: hidden; background-color: #143B64; color: #fff; text-align: center; padding: 5px 4px; border-radius: 6px; max-width: 50%; min-width: 125px; bottom: 77%; left: 18%; margin-left: -60px; position: absolute; z-index: 1; font-size: 1.3rem; text-transform: capitalize; }

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext { visibility: visible; }

