.simplebar-scrollbar { width: 4px; }

.simplebar-scrollbar:before { background: #b9b8b8 !important; }

.fc-button-primary, .fc-button-primary:hover, .fc-button-primary:active, .fc-button-primary:focus { color: #0B63AC !important; background-color: transparent !important; border-color: transparent !important; border: none !important; box-shadow: unset !important; }

.fc-button-group { margin-top: 1rem; }

.fc-toolbar.fc-header-toolbar { margin-bottom: 2em; }

