.fc-button.fc-button-primary.fc-next-button { background-image: url("../img/icons/right_b.png"); }

.fc-left { display: flex; align-items: center; }

.fc-today-button { color: #0B63AC; font-size: 14px; text-transform: capitalize; font-weight: bold; padding: 0; }

.fc-event, .fc-event:hover { background-color: transparent; color: #58595B; border: unset; }

.ev_cont { position: fixed; top: 0; left: 0; width: 100vw; height: 100vh; background: rgba(0, 0, 0, 0.7); display: flex; align-items: center; justify-content: center; z-index: 9999; }

.ev_cont > div { width: 50%; min-height: 200px; padding: 2rem; background-color: white; position: relative; }

.ev_cont > div #closeEve { position: absolute; top: 2rem; right: 2rem; }

.ev_cont > div #closeEve i { font-size: 1.8rem; color: #58595B; cursor: pointer; }

.ev_cont > div #startDate { color: #58595B; font-size: 14px; font-weight: 700; display: block; margin-bottom: .3rem; }

