.ev_cont > div .fromTo .to { margin: 0 .3rem; }

.ev_cont > div img { width: 14px; margin-right: .3rem; }

.ev_cont > div #eventInfo { color: #58595B; font-size: 15px; }

.ev_cont > div .evDesc { display: flex; align-items: center; }

.fc-event-container .fc-content { display: flex; flex-direction: column; margin-bottom: .5rem; }

.fc-event-container .fc-content .fc-time { font-size: 10px; color: #58595B; }

.fc-event-container .fc-content .fc-title { padding-top: 1px; font-size: 11px; color: #58595B; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; width: 100%; }

.fc-unthemed td.fc-today { background: unset; }

.irHome ~ .popper, .irHome ~ .tooltip { position: absolute; z-index: 9999; border-color: rgba(0, 0, 0, 0.95); color: #FFFFFF; width: 150px; border-radius: 3px; box-shadow: 0 0 2px rgba(0, 0, 0, 0.5); padding: 10px; text-align: center; background-color: rgba(0, 0, 0, 0.95); }

.irHome ~ .popper .popper__arrow, .irHome ~ .popper .tooltip-arrow, .irHome ~ .tooltip .popper__arrow, .irHome ~ .tooltip .tooltip-arrow { width: 0; height: 0; border-style: solid; position: absolute; margin: 5px; display: none; }

.irHome ~ .popper .tooltip-arrow, .irHome ~ .popper .popper__arrow, .irHome ~ .tooltip .tooltip-arrow, .irHome ~ .tooltip .popper__arrow { border-color: rgba(0, 0, 0, 0.95); }

