.irHome ~ .popper[x-placement^="top"], .irHome ~ .tooltip[x-placement^="top"] { margin-bottom: 5px; }

.irHome ~ .popper[x-placement^="top"] .popper__arrow, .irHome ~ .popper[x-placement^="top"] .tooltip-arrow, .irHome ~ .tooltip[x-placement^="top"] .popper__arrow, .irHome ~ .tooltip[x-placement^="top"] .tooltip-arrow { border-width: 5px 5px 0 5px; border-left-color: transparent; border-right-color: transparent; border-bottom-color: transparent; bottom: -5px; left: calc(50% - 5px); margin-top: 0; margin-bottom: 0; }

.irHome ~ .popper[x-placement^="bottom"], .irHome ~ .tooltip[x-placement^="bottom"] { margin-top: 5px; }

.irHome ~ .popper[x-placement^="bottom"] .tooltip-arrow, .irHome ~ .popper[x-placement^="bottom"] .popper__arrow, .irHome ~ .tooltip[x-placement^="bottom"] .tooltip-arrow, .irHome ~ .tooltip[x-placement^="bottom"] .popper__arrow { border-width: 0 5px 5px 5px; border-left-color: transparent; border-right-color: transparent; border-top-color: transparent; top: -5px; left: calc(50% - 5px); margin-top: 0; margin-bottom: 0; }

.irCalendarWrap.investorContainItem .fc-content { font-size: 0; overflow: hidden; height: 4px; width: 4px; background: #0B63AC; border-radius: 100rem; margin-left: .5rem; margin-bottom: 0.1rem; }

.img_container { width: 100%; display: flex; flex-wrap: wrap; justify-content: space-between; }

.img_container > p { width: 32%; margin-bottom: 1rem !important; }

@media (max-width: 75em) { .img_container > p { width: 49%; } }

@media (max-width: 37.5em) { .img_container > p { width: 100%; } }

.img_container > p img { width: 100%; object-fit: cover; }
