*,*::after,*::before{
    box-sizing: border-box;
}
html{
    font-size:62.5%;
    @include respond(tab-land){
        font-size: 56.25%;
    }
    @include respond(tab-port){
        font-size: 50%;
    }
    @include respond(big-desktop){
        font-size: 75%;
    }
}
body{
    position: relative;
    overflow-x: hidden;
    box-sizing: border-box;
    margin: 0 0 ;
   
    @include respond(tab-port){
        padding: 0px;
        margin: 0px;
    }
}
.clearfix{
    clear: both;
}

.overflowHidden {
    overflow: hidden;
}


button,a {
    cursor: pointer;
}
